.App{
  min-height: 100vh;
  background-image: linear-gradient(45deg , #fbc2eb 1%, #a6c1ee 100%);


}


.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button{
background: lightcoral;
border: none;
padding: 10px 20px;
color: #a6c1ee;
}

.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}